  get f(){
    // console.log(this.countryForm.controls)
    return this.subAdminForm.controls;}


  get g () {
    return this.genLoginForm.controls;
  }

  getAllSubAdminList() {
    this.api.getAllSubAdminList().subscribe(
      (res) => {
        console.log(res, "::::::::::::::::;:::after api");
        console.log(this.dataSource, "lll:::::::::::::::::::::");
        this.subAdminList = res["response"];
        console.log(this.subAdminList, "list");
        this.dataSource = new MatTableDataSource(res["response"]);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err) => {
        console.log(err);
      }
    ); 

   
    
  }

  setSubmittedFalse(){
    this.submitted = false;
  }

  
